
import { AlertType } from '@/store/ourExtension/layoutsData/alerts/types';
import { Prop, Component, Vue } from 'vue-property-decorator';

@Component({
    components: {

    },
})
export default class DefaultAlert extends Vue {
    get alert(): AlertType {
        return this.$store.getters['ourExtension/layoutsData/alerts/getFirstAlert']()
    }

    confirmClick() {
        this.$store.dispatch('ourExtension/layoutsData/alerts/confirmClick')
    }

    rejectClick() {
        this.$store.dispatch('ourExtension/layoutsData/alerts/rejectClick')
    }
}
